import {
  Component,
  OnDestroy,
} from '@angular/core';

import { DialogService } from 'ng2-bootstrap-modal';
import { Subscription } from 'rxjs';

import { RegistrationComponent } from '../../../registration/pages/registration/registration.component';
import { OpenModalService } from '../../../../core/services/open-modal.service';

@Component({
  selector: 'app-property-valuation-register-now',
  templateUrl: './property-valuation-register-now.component.html',
  styleUrls: [
    './property-valuation-register-now.component.css',
  ],
})
export class PropertyValuationRegisterNowComponent implements OnDestroy {
  public name: string = 'property-valuation-register-now';

  private subscriptions: Subscription = new Subscription();

  constructor(
    private dialogService: DialogService,
    private openModalService: OpenModalService,
  ) {
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  public registerAsOwner(): void {
    const dialogOptions = {
      role: 'owner',
      mode: 'registration',
    };
    this.openModalService.open();
    const subscription = this.dialogService.addDialog(RegistrationComponent, dialogOptions)
      .subscribe(() => {
        this.openModalService.close();
      });
    this.subscriptions.add(subscription);
  }
}
